  overflow: hidden;
  margin: 0, auto;
  text-align: center;
}

header {
  color: rgb(255, 255, 255);
  position: absolute;
  text-align: center;
  z-index: 3;
  margin-left: 47.5%;
  margin-top: 200px;
  text-align: center;
}

footer{
  color: rgb(255, 255, 255);
  position: absolute;
  margin-top: 400px;
